import moment from 'moment'
export default {
  name: 'PartitionMetric',
  props: {
    metric: {
      type: Object,
      required: true
    }
  },

  data () {
    return {
      filterLabel: 'Today',
      customDate: false,
      dateRange: {
        start_date: null,
        end_date: null
      },
      query: {
        startDate: moment().subtract(7, 'days').format('YYYY-MM-DD'),
        endDate: null
      },
      datacollection: null,
      options: {
        maintainAspectRatio: false,
        plugins: {
          colorschemes: {
            scheme: 'office.Studio6'
          }
        },
        cutoutPercentage: 70,
        legend: {
          position: 'left',
          labels: {
            boxWidth: 9,
            usePointStyle: true
          }
        }
      }
    }
  },

  mounted () {
    this.fillData()
  },

  methods: {
    setCustomDate () {
      this.customDate = true
    },
    setDaysAgo (days, label = 'Today') {
      this.query.startDate = moment().subtract(days, 'days').format('YYYY-MM-DD')
      this.query.endDate = null
      this.customDate = false
      this.filterLabel = label
    },
    fillData () {
      this.datacollection = {
        labels: ['Silver', 'Gold', 'Platinum'],
        datasets: [
          {
            data: [4, 23, 10]
          }
        ]
      }
    },
    getRandomInt () {
      return Math.floor(Math.random() * (50 - 5 + 1)) + 5
    },

    removeMetric () {
      this.$swal.fire({
        text: 'Are you sure you want to remove this metric?',
        buttons: true,
        dangerMode: true,
        preConfirm: () => this.$axios.$delete(`dashboard/metric/${this.metric.id}/remove`)
          .then((res) => {
            this.$toast.success('Metric removed!')
            window.location.reload()
          }).catch((err) => {
            this.$toast.error(err.response.data.errors)
          })
      })
    }
  }
}
